import * as React from 'react';
import { styled } from '@mui/system';
import { ButtonBase } from '@mui/material';

const StyleBtn = styled(ButtonBase)(({ theme }) => ({
  minWidth: '70px',
  fontSize: theme.font.size.sm,
  fontWeight: theme.font.weight.bold,
  color: theme.button.tertiary.color,
  background: theme.button.tertiary.background,
  border: `1px solid ${theme.button.tertiary.border}`,
  borderRadius: theme.button.common.radius,
  padding: '5px 10px',
  transition: 'all 0.3s ease',
  '&:hover, &:active': {
    transform: 'scale(0.95)',
    boxShadow: theme.button.common.shadow.none,
    border: `1px solid ${theme.button.tertiary.hover.border}`,
    background: theme.button.tertiary.hover.background,
    color: theme.button.tertiary.hover.color,
  },
  '&:disabled': {
    background: theme.button.tertiary.disabled.background,
    color: theme.button.tertiary.disabled.color,
    borderColor: theme.button.tertiary.disabled.border,
    boxShadow: theme.button.common.shadow.none,
  },

}));

const TertiaryBtn = React.forwardRef((props, ref) => {
  return (
    <>
      <StyleBtn ref={ref} {...props} />
    </>
  );
});

export default TertiaryBtn;