import * as React from 'react';
import { styled } from '@mui/system';
import { ButtonBase } from '@mui/material';

const StyleBtn = styled(ButtonBase)(({ theme }) => ({
  minWidth: '70px',
  width: theme.button.common.width.sm,
  height: theme.button.common.height.sm,
  padding: theme.button.common.paddingSize.sm,
  fontSize: theme.font.size.sm,
  fontWeight: theme.font.weight.bold,
  color: theme.button.secondary.color,
  background: theme.button.secondary.background,
  border: `1px solid ${theme.button.secondary.border}`,
  borderRadius: theme.button.common.radius,
  transition: 'all 0.3s ease',
  whiteSpace: 'nowrap',
  '&:hover, &:active': {
    transform: 'scale(0.95)',
    boxShadow: theme.button.common.shadow.none,
    borderColor: theme.button.secondary.hover.border,
    background: theme.button.secondary.hover.background,
    // color: theme.button.secondary.hover.color,
  },
  '&:disabled': {
    background: theme.button.secondary.main,
    color: theme.button.secondary.disabled.color,
    borderColor: theme.button.secondary.disabled.none,
    boxShadow: theme.button.common.shadow.none,
  },

}));

const SecondBtnSM = React.forwardRef((props, ref) => {
  return (
    <>
      <StyleBtn ref={ref} {...props} />
    </>
  );
});

export default SecondBtnSM;