import authApi from './authApi';

// 유저 회원가입(등록)
export const registerFn = async (sign) => {
    const res = await authApi.post('user/register', sign);
    return res.data;
};

// 아이디 중복 체크
export const checkIdFn = async (username) => {
    const res = await authApi.get(`user/is_username_duplicate/${username}`);
    return res.data;
};

// 유저 로그인
export const loginFn = async (user) => {
    const res = await authApi.post('user/login', user);
    return res.data;
};

// 협회 로그인
export const AssociationLoginFn = async ([assoName, body]) => {
  const res = await authApi.post(`user/login?association_name=${assoName}&country_code=KOR`, body)
  return res.data;
}

export const getMyInfo = async (userId) => {
  const res = await authApi.get(`user/finduser/${userId}`)
  return res.data
}

export const sendSMSforId = async (phone_number) => {
  const res = await authApi.post(`util/sendsms_username/${phone_number}`)
  return res.data;
}

// 유저 아이디 찾기
export const findUserName = async (body) => {
  const res = await authApi.post('util/findusername_sms', body)
  return res.data;
}

// 유저 비밀번호 찾기
export const sendSMSforPw = async (username) => {
  const res = await authApi.get(`util/sendsms_password/${username}`)
  return res.data;
}

// 유저 비밀번호 재설정
export const resetUserPW = async (body) => {
  const res = await authApi.post('util/reset_password', body)
  return res.data;
}

export const changePW = async (body) => {
  const res = await authApi.post('user/changemyinfo', body)
  return res.data;
}

export const changeEmail = async (body) => {
  const res = await authApi.post('user/changemyinfo', body)
  return res.data;
}

export const changePhone = async (body) => {
  const res = await authApi.post('user/changephonenumber', body)
  return res.data;
}

export const getLinkedInfo = async () => {
  const res = await authApi.get('user/is_linked')
  return res.data
}


