import { Typography } from '@mui/material';
import React, { useState } from 'react';
import PageContainer from '../../../components/layout/PageContainer';
import GrayBtnLong from '../../../components/UI/Atoms/Button/GrayBtnLong';
import PrimaryBtnSM from '../../../components/UI/Atoms/Button/PrimaryBtnSM';
import TertiaryBtn from '../../../components/UI/Atoms/Button/TertiaryBtn';
import { useNavigate } from 'react-router-dom';
import { useModal } from '../../../hooks/basic/useModal';
import InputCover from '../../../components/UI/Atoms/Input/InputCover';
import ModalContainerAlert from '../../../components/layout/ModalContainerAlert';
import AlertText from '../../../components/common/Alert/AlertText';
import { useMutation } from '@tanstack/react-query';
import { resetUserPW, sendSMSforPw } from '../../../api/user';
import { useDebug } from '../../../hooks/basic/useDebug';

const FindUserPW = () => {

  const navigate = useNavigate();

  const regPhone = new RegExp(/^\d{11}$/);

  const [saved, setSaved] = useState(false);
  const [open, openFn, closeFn] = useModal();
  const [allData, setAllData] = useState({
    username: '',
    new_password: '',
    number_by_user: '',
  });

  const { data, mutate: SMSMT } = useMutation((username) => sendSMSforPw(username));

  const { mutate: resetPw } = useMutation((body) => resetUserPW(body));

  const handlePhoneNumber = () => {
    const uName = document.getElementById('username').value;
    const phone = document.getElementById('phone_number').value;

    setAllData({ username: uName });

    if ((phone.length >= 11) && (regPhone.test(phone) == true)) {
      SMSMT(uName, {
        onSuccess: (res) => {
          alert('인증번호 전송완료');
          console.log(res);
        },
      });
    }
    if ((phone.length < 11) && (regPhone.test(phone) == false)) {
      alert('휴대폰 번호를 다시 확인해주세요.');
    }
  };


  const handleSave = () => {
    const num = document.getElementById('sms').value;
    const pw = document.getElementById('newPw').value;
    const newPw = document.getElementById('newPw2').value;
    const temp = { ...allData, number_by_user: num , new_password: pw };

    if (pw == newPw) {
      resetPw(temp, {
        onSuccess: (res) => {
          if(res == 'wrong access or expired'){
            alert('인증번호를 다시 확인해주세요.');
            // setSaved(false);
          }
          if(res == 'password successfully updated'){
            setSaved(true);
          }
        },
      });
    }

    if(pw != newPw){
      alert('새로운 비밀번호가 일치하지 않습니다.')
    }

  };


  const handleHome = () => {
    navigate('/');
  };


  // useDebug(allData)

  return (
    <>
      <PageContainer width="md">
        <Typography variant="h3" className="mt-[25px]">
          비밀번호 찾기
        </Typography>

        {!saved && (
          <div>
            <Typography variant="h4" className="text-center my-[50px]">
              * 회원 정보에 등록한 휴대전화 번호와 <br></br>
              입력한 휴대전화 번호가 같아야, 인증번호를 받을 수 있습니다.
            </Typography>
            <div>
              <div className="w-[500px]">
                <InputCover
                  id="username"
                  className="w-full mb-[25px]"
                  label="아이디"
                  name="username"
                ></InputCover>
                <div className="flex items-baseline gap-[10px] mb-[25px]">
                  <InputCover
                    type="tel"
                    id="phone_number"
                    label="전화번호"
                    name="phone_number"
                    variant="outlined"
                    fullWidth
                    placeholder="전화번호를 입력해주세요."
                    inputProps={{ maxLength: 11 }}
                  />
                  <PrimaryBtnSM onClick={handlePhoneNumber} sx={{ height: 50 }}>
                    인증요청
                  </PrimaryBtnSM>
                </div>
                <div className="flex items-baseline gap-[10px]">
                  <InputCover
                    id="sms"
                    className="w-full mb-[25px]"
                    label="인증번호"
                    name="sms"
                    variant="outlined"
                    fullWidth
                    placeholder="인증번호를 입력해주세요."
                    inputProps={{ maxLength: 4 }}
                  />
                </div>

                <InputCover
                  id="newPw"
                  className="w-full mb-[25px]"
                  label="새 비밀번호"
                  name="newPw"
                  variant="outlined"
                  fullWidth
                  placeholder="8~20자의 영문 대/소문자, 숫자, 특수문자로 설정해주세요."
                  // inputProps={{ maxLength: 11 }}
                />
                <InputCover
                  id="newPw2"
                  label="새 비밀번호 재입력"
                  name="newPw2"
                  variant="outlined"
                  fullWidth
                  placeholder="8~20자의 영문 대/소문자, 숫자, 특수문자로 설정해주세요."
                  // inputProps={{ maxLength: 11 }}
                />
                <GrayBtnLong className="w-[500px] h-[40px] mt-[50px] bg-[#ffffff] " onClick={handleSave}>
                  저장하기
                </GrayBtnLong>
              </div>

            </div>
          </div>
        )}

        {saved && (
          <div className="flex flex-col items-center">
            <Typography variant="h4" className="text-center my-[50px]">
              * 비밀번호가 재설정 되었습니다. <br></br>
              다시 한번 로그인 해 주세요.
            </Typography>
            <TertiaryBtn className="w-[140px] h-[50px] text-[17px] mr-[10px]" onClick={handleHome}>
              홈으로 가기
            </TertiaryBtn>
          </div>
        )}

        <ModalContainerAlert open={open} onClose={closeFn} onConfirm={closeFn} confirmText="확인">
          <AlertText title>아이디가 일치하지 않습니다.</AlertText>
          <AlertText>
            회원 정보에 등록한 전화번호와 아이디가 <br />
            일치하지 않습니다 <br />
            다시 한 번 확인해주세요.
          </AlertText>
        </ModalContainerAlert>
      </PageContainer>
    </>
  );
};

export default FindUserPW;
