import React from 'react';
import { styled } from '@mui/system';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import PrimaryBtnSM from '../UI/Atoms/Button/PrimaryBtnSM';
import SecondBtnSM from '../UI/Atoms/Button/SecondBtnSM';

const BoxStyle = styled(Box)(({ theme, width, height }) => ({
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: width,
  height: height,
  display: 'flex',
  flexDirection: 'column',
  padding: '40px 30px',
  backgroundColor: '#ffffff',
  borderRadius: '10px',
  boxShadow: 24,
  overflow: 'auto',
}));

const ModalContainerAlert = ({ open, onClose, onConfirm, confirmText, isLoading, children, width, height }) => {

  return (
    <>
      <Modal open={open} onClose={onClose}>
        <BoxStyle width={width || 400} height={height || 280}>
          {children}

        <div className='absolute bottom-[38px] right-[30px] flex mt-[20px] justify-end gap-[5px]'>
          <SecondBtnSM onClick={onClose}>취소</SecondBtnSM>
          <PrimaryBtnSM onClick={onConfirm} disabled={isLoading}>{confirmText}</PrimaryBtnSM>
        </div>
      </BoxStyle>
    </Modal>
    </>
  );
};

export default ModalContainerAlert;
