import * as React from 'react';
import { styled } from '@mui/system';
import { ButtonBase } from '@mui/material';

const StyleBtn = styled(ButtonBase)(({ theme }) => ({
  minWidth: '70px',
  height: '40px',
  padding: theme.button.common.paddingSize.long,
  fontSize: theme.font.size.sm,
  fontWeight: theme.font.weight.bold,
  color: '#555555',
  background: '#F2F2F2',
  border: '1px solid #AAAAAA',
  borderRadius: theme.button.common.radius,
  transition: 'all 0.3s ease',
  '&:hover, &:active': {
    transform: 'scale(0.95)',
    boxShadow: 'none',
    borderColor: theme.button.secondary.hover.border,
    background: '#F2F2F2',
    color: '#555555',
  },
  '&:disabled': {
    background: theme.button.secondary.main,
    color: theme.button.secondary.disabled.color,
    borderColor: theme.button.secondary.disabled.none,
    boxShadow: theme.button.common.shadow.none,
  },

}));

const GrayBtnLong = React.forwardRef((props, ref) => {
  return (
    <>
      <StyleBtn ref={ref} {...props} />
    </>
  );
});

export default GrayBtnLong;